/* -- Header -- */
.header {
  position: relative;
  display: block;
  /* -- Navigation -- */
}

.header-top-spot {
  height: 60px;
}

.header .navbar {
  justify-content: flex-start;
  flex-direction: column;
  line-height: 60px;
  position: relative;
  flex-wrap: nowrap;
  height: 60px;
  flex-shrink: 0;
  z-index: 1200;
  width: 100%;
}

.header .navbar-layout {
  display: flex;
}

.header .navbar-fixed {
  position: fixed;
  left: 0;
  top: 0;
}

.header .navbar-head {
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  flex-shrink: 0;
  display: flex;
  left: 0;
  top: 0;
}

.header .navbar-title {
  font-family: "Roboto", sans-serif;
  letter-spacing: 0.02em;
  text-decoration: none;
  position: relative;
  font-size: 17px;
  flex-shrink: 0;
  margin: 5px 0;
}

.header .navbar-list {
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  list-style: none;
  display: flex;
  margin: 0;
}

.header .navbar-link {
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
  padding: 0 28px;
  font-size: 14px;
  display: block;
  color: #000;
}

.header .navbar-link:hover {
  color: #ddd !important;
}

.header .navbar-link-focus {
  color: #bbb;
}

/* -- Styles (only affect text) -- */
.style-title, .style-title-font, .style-font-title, .style-font-title-font {
  font-family: Roboto Slab, sans-serif;
}

.style-light, .style-light-font, .style-font-light, .style-font-light-font {
  font-family: Montserrat, sans-serif;
}

.style-icon, .style-icon-font, .style-font-icon, .style-font-icon-font {
  font-family: Material Icons, sans-serif;
}

.style-regular, .style-regular-font, .style-font-regular, .style-font-regular-font {
  font-family: Roboto, sans-serif;
}

.style-bold, .style-bold-font, .style-font-bold, .style-font-bold-font {
  font-family: Montserrat, sans-serif;
}

.style, .style-sz, .style-size, .style-font, .style-font-sz, .style-font-size {
  font-size: 14px;
}

.style-8, .style-sz-8, .style-size-8, .style-font-8, .style-font-sz-8, .style-font-size-8 {
  font-size: 8px;
}

.style-9, .style-sz-9, .style-size-9, .style-font-9, .style-font-sz-9, .style-font-size-9 {
  font-size: 9px;
}

.style-10, .style-sz-10, .style-size-10, .style-font-10, .style-font-sz-10, .style-font-size-10 {
  font-size: 10px;
}

.style-11, .style-sz-11, .style-size-11, .style-font-11, .style-font-sz-11, .style-font-size-11 {
  font-size: 11px;
}

.style-12, .style-sz-12, .style-size-12, .style-font-12, .style-font-sz-12, .style-font-size-12 {
