.transition-fade {
  transition: opacity 0.2s ease, transform 0.4s ease 0.1s;
  transform: translateY(0px);
  opacity: 1;
}

.is-animating .transition-fade {
  transform: translateY(-20px) !important;
  opacity: 0 !important;
}

